import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { NavLink, useParams } from "react-router-dom";
import * as firebase from "firebase";
import { FirebaseConnect } from "../../FirebaseConnect";
import Grid from "@material-ui/core/Grid";
import DateFnsUtils from "@date-io/date-fns"; // choose your lib
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";

const BuyProducts = (props) => {
  let { id } = useParams();
  const [buy, setBuy] = useState([]);
  //info
  const [status, setStatus] = useState(false);
  const [firthName, setFirthName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [quan, setQuan] = useState("");
  const [tp, setTp] = useState("");
  const [phuong, setPhuong] = useState("");
  const [online, setOnline] = useState("Offline");
  const [selectedDate, setSelectedDate] = useState(new Date());
  
  useEffect(() => {
    const connectData = firebase.database().ref("confirm");
    connectData.on("value", (data) => {
      console.log(data.val());
      const arr = [];
      data.forEach((e) => {
        arr.push({
          id: e.key,
          name: e.val().name,
          price: e.val().price,
          image: e.val().image,
          type: e.val().type,
        });
      });
      setBuy(arr);
    });
  }, []);

  // const index = ;
  // console.log(index);

  const check = () => {
    if(status){
      buy.forEach((e, key) => {
        if (id == e.id) {
          const date = new Date();
          const connectData = firebase.database().ref("order");
          connectData.push({
            firthname: firthName,
            lastname: lastName,
            email: email,
            phone: phone,
            address: address,
            quan: quan,
            tp: tp,
            phuong: phuong,
            typebuy: online,
            product: buy[key],
            date: date.toString(),
            dateto: selectedDate.toString(),
          });
        }
      });
    } else {
      alert("bn vui lng 'check me out'  xc nhn mua hng.");
    }
  };
  
  return (
    <div className="container">
      <div className="text-center">
        <h3 className="display-4">Xc nhn thng tin</h3>
      </div>
      <div class="jumbotron jumbotron-fluid mt-2">
        <div class="container">
          <div className="row">
            {buy.map((value, key) => {
              if (id == value.id) {
                return (
                  <div className="col-lg-5">
                    <h2>Xc nhn dch v</h2>
                    <h5>Hnh nh:</h5>
                    <img
                      src={value.image}
                      class="img-fluid rounded"
                      alt=""
                      style={{ maxWidth: "70%" }}
                    />
                    <h5>
                      Tn dch v/ sn phm: <p className="h3">{value.name}</p>{" "}
                    </h5>
                    <p className="h5">
                      th loi:{" "}
                      <h3>{value.type == "sp" ? "sn phm" : "dch v"}</h3>
                    </p>
                    <p className="h5">
                      Gi: <h3>{value.price}</h3>
                    </p>
                    {value.type == "sp" ? (
                      <div>
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="exampleRadios"
                            id="exampleRadios1"
                            value="Online"
                            onClick={(e)=>{setOnline(e.target.value)}}
                          />
                          <label
                            className="form-check-label"
                            htmlFor="exampleRadios1"
                          >
                            Mua online
                          </label>
                        </div>
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="exampleRadios"
                            id="exampleRadios2"
                            value="Offline"
                            onClick={(e)=>{setOnline(e.target.value)}}
                          />
                          <label
                            className="form-check-label"
                            htmlFor="exampleRadios2"
                          >
                            Mua offline
                          </label>
                        </div>
                      </div>
                    ) : null}
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <Grid container justify="space-around">
                        <KeyboardDatePicker
                          margin="normal"
                          id="date-picker-dialog"
                          label="t ngy"
                          format="MM/dd/yyyy"
                          value={selectedDate}
                          onChange={(date) => {
                            setSelectedDate(date);
                          }}
                          KeyboardButtonProps={{
                            "aria-label": "change date",
                          }}
                        />
                        <KeyboardTimePicker
                          margin="normal"
                          id="time-picker"
                          label="t gi"
                          value={selectedDate}
                          onChange={(date) => {
                            setSelectedDate(date);
                          }}
                          KeyboardButtonProps={{
                            "aria-label": "change time",
                          }}
                        />
                      </Grid>
                    </MuiPickersUtilsProvider>
                  </div>
                );
              }
            })}

            <div className="col-lg-7">
              <form>
                <div className="form-row">
                  <div className="form-group col-md-6">
                    <label htmlFor="inputEmail4">H</label>
                    <input
                      type="email"
                      className="form-control"
                      id="inputEmail4"
                      onChange={(e) => {
                        setFirthName(e.target.value);
                      }}
                    />
                  </div>
                  <div className="form-group col-md-6">
                    <label htmlFor="inputPassword4">Tn</label>
                    <input
                      type="text"
                      className="form-control"
                      id="inputPassword4"
                      onChange={(e) => {
                        setLastName(e.target.value);
                      }}
                    />
                  </div>
                </div>
                <div className="form-group">
                  <label htmlFor="inputAddress">Email</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputAddress"
                    placeholder="abc@gmail.com"
                    onChange={(e) => {
                      setEmail(e.target.value);
                    }}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="inputAddress">S in thoi</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputAddress"
                    placeholder="0902356022"
                    onChange={(e) => {
                      setPhone(e.target.value);
                    }}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="inputAddress2">a ch</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputAddress2"
                    placeholder="83 Nguyn Tt Thnh"
                    onChange={(e) => {
                      setAddress(e.target.value);
                    }}
                  />
                </div>
                <div className="form-row">
                  <div className="form-group col-md-6">
                    <label htmlFor="inputCity">Thnh Ph / Tnh</label>
                    <input
                      type="text"
                      className="form-control"
                      id="inputCity"
                      onChange={(e) => {
                        setTp(e.target.value);
                      }}
                    />
                  </div>
                  <div className="form-group col-md-6">
                    <label htmlFor="inputState">Qun / Huyn </label>
                    <input
                      type="text"
                      className="form-control"
                      id="inputState"
                      onChange={(e) => {
                        setQuan(e.target.value);
                      }}
                    />
                  </div>
                  <div className="form-row">
                    <div className="form-group col-md-12">
                      <label htmlFor="inputZip">Phng / X</label>
                      <input
                        type="text"
                        className="form-control"
                        id="inputZip"
                        onChange={(e) => {
                          setPhuong(e.target.value);
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id="gridCheck"
                      onClick={()=>{setStatus(!status)}}
                    />
                    <label className="form-check-label" htmlFor="gridCheck">
                      Check ti y  xc nhn mua hng
                    </label>
                  </div>
                </div>
              </form>
            </div>
          </div>
          <hr class="my-2" />
          <p>vui lng xc nhn s dng dch v ti ca hng</p>
          <div className="row">
            <div className="col-lg-5">
              <NavLink to="/home">
                <button
                  type="button"
                  class="btn btn-primary mr-3"
                  onClick={() => {
                    check();
                  }}
                >
                  Xc nhn
                </button>
              </NavLink>
              <NavLink to="/home">
                <button type="button" class="btn btn-danger">
                  Hy b
                </button>
              </NavLink>
            </div>
            <div className="col-lg-7">
              <h6>
                mi chi tit vui lng lin h: 0902356022 (nhn vin t vn)
              </h6>
              <h6>thi gian lm vic: 8h - 16h </h6>
              <h6>
                a ch ca hng: 83 Nguyn Tt Thnh, Phng 6, Qun 4, TP.HCM
              </h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

BuyProducts.propTypes = {};

export default BuyProducts;
